import axios from '../utils/axios';

class ConsumeChopRuleService {
  list = async () => {
    const consumeChopRules = await axios.get('/api/v1/consumeChopRules');
    return consumeChopRules;
  };

  create = async (data) => {
    const consumeChopRule = await axios.post('/api/v1/consumeChopRules', data);
    return consumeChopRule;
  };

  get = async (id) => {
    const consumeChopRule = await axios.get(`/api/v1/consumeChopRules/${id}`);
    return consumeChopRule;
  };

  update = async (id, data) => {
    const consumeChopRule = await axios.patch(`/api/v1/consumeChopRules/${id}`, data);
    return consumeChopRule;
  };

  delete = async (id) => {
    const consumeChopRule = await axios.delete(`/api/v1/consumeChopRules/${id}`);
    return consumeChopRule;
  };
}

const consumeChopRuleService = new ConsumeChopRuleService();

export default consumeChopRuleService;
