import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useRank from '../../../hooks/useRank';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import ConsumeChopRuleCreateForm from './ConsumeChopRuleCreateForm';
import Header from './Header';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import consumeChopRuleService from '../../../services/consumeChopRuleService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function ConsumeChopRuleEditView() {
  const classes = useStyles();
  const { ranks } = useRank();
  const { t } = useTranslation();

  if (!ranks.length) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Create Consume Chop Rules')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <ConsumeChopRuleCreateForm ranks={ranks} />
        </Box>
      </Container>
    </Page>
  );
}

export default ConsumeChopRuleEditView;
