import axios from '../utils/axios';

class RankService {
  list = async () => {
    const ranks = await axios.get('/api/v1/ranks');
    return ranks;
  };

  create = async (data) => {
    const rank = await axios.post('/api/v1/ranks', data);
    return rank;
  };

  get = async (id) => {
    const rank = await axios.get(`/api/v1/ranks/${id}`);
    return rank;
  };

  update = async (id, data) => {
    const rank = await axios.patch(`/api/v1/ranks/${id}`, data);
    return rank;
  };

  delete = async (id) => {
    const rank = await axios.delete(`/api/v1/ranks/${id}`);
    return rank;
  };
}

const rankService = new RankService();

export default rankService;
