import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useHistory } from 'react-router';
import useSnackbar from '../../../hooks/useSnackbar';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { DateTimePicker } from '@material-ui/pickers';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { paymentTypes } from '../../../constants/paymentTypes';
import { ruleTypes } from '../../../constants/ruleTypes';
import moment from 'moment-timezone';
import { useTranslation } from 'react-i18next';
import consumeChopRuleService from '../../../services/consumeChopRuleService';

const useStyles = makeStyles(() => ({
  root: {}
}));

function ConsumeChopRuleCreateForm({ className, ranks, ...rest }) {
  const classes = useStyles();
  const { successSnackbar } = useSnackbar();
  const { t } = useTranslation();
  const history = useHistory();

  return (
    <Formik
      initialValues={{
        name: '',
        description: '',
        payment_type: 'ALL',
        type: 'AMOUNT',
        chops_per_unit: 1,
        unit_per_amount: 1,
        consume_max_percentage: 100,
        earn_chops_after_consume: false,
        rank_id: ranks[0].id,
        activated_at: moment().format('YYYY-MM-DD HH:mm:ss'),
        expired_at: moment().format('YYYY-MM-DD HH:mm:ss')
      }}
      validationSchema={Yup.object().shape({
        name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') })),
        description: Yup.string().max(255),
        payment_type: Yup.string().oneOf(['ALL', 'LINE_PAY', 'CASH']),
        type: Yup.string().oneOf(['AMOUNT', 'ITEM_COUNT']),
        chops_per_unit: Yup.number().min(1),
        unit_per_amount: Yup.number().min(0),
        consume_max_percentage: Yup.number()
          .max(100)
          .min(1),
        earn_chops_after_consume: Yup.bool()
      })}
      onSubmit={async (values, { resetForm, setErrors, setStatus, setSubmitting }) => {
        try {
          await await consumeChopRuleService.create({
            ...values
          });
          setStatus({ success: true });
          setSubmitting(false);
          successSnackbar(t('Created', { name: t('Consume Chop Rule') }));
          history.push('/consumeChopRules');
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
        setFieldValue
      }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label={t('Name')}
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.description && errors.description)}
                    fullWidth
                    helperText={touched.description && errors.description}
                    label={t('Description')}
                    name="description"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.description}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Rank')}
                    name="rank_id"
                    onChange={handleChange}
                    select
                    required
                    SelectProps={{ native: true }}
                    value={values.rank_id}
                    variant="outlined"
                  >
                    {ranks.map((rank) => (
                      <option key={rank.id} value={rank.id}>
                        {rank.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Payment Type')}
                    name="payment_type"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.payment_type}
                    variant="outlined"
                  >
                    {paymentTypes.map((paymentType) => (
                      <option key={paymentType} value={paymentType}>
                        {t(`PaymentType.${paymentType}`)}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Type')}
                    name="type"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.type}
                    variant="outlined"
                  >
                    {ruleTypes.map((ruleType) => (
                      <option key={ruleType} value={ruleType}>
                        {t(`Type.${ruleType}`)}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.chops_per_unit && errors.chops_per_unit)}
                    fullWidth
                    helperText={touched.chops_per_unit && errors.chops_per_unit}
                    label={t('Chops Per Unit')}
                    name="chops_per_unit"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.chops_per_unit}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.unit_per_amount && errors.unit_per_amount)}
                    fullWidth
                    helperText={touched.unit_per_amount && errors.unit_per_amount}
                    label={t('Unit Per Amount')}
                    name="unit_per_amount"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.unit_per_amount}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.consume_max_percentage && errors.consume_max_percentage)}
                    fullWidth
                    helperText={touched.consume_max_percentage && errors.consume_max_percentage}
                    label={t('Consume Max Percentage')}
                    name="consume_max_percentage"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.consume_max_percentage}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12} />
                <Grid item md={6} xs={12}>
                  <DateTimePicker
                    error={Boolean(touched.activated_at && errors.activated_at)}
                    fullWidth
                    helperText={touched.activated_at && errors.activated_at}
                    value={values.activated_at}
                    label={t('Activated At')}
                    name="activated_at"
                    onBlur={handleBlur}
                    format="YYYY-MM-DD HH:mm:ss"
                    required
                    onChange={(date) =>
                      setFieldValue('activated_at', moment(date).format('YYYY-MM-DD HH:mm:ss'))
                    }
                    inputVariant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <DateTimePicker
                    error={Boolean(touched.expired_at && errors.expired_at)}
                    fullWidth
                    helperText={touched.expired_at && errors.expired_at}
                    value={values.expired_at}
                    label={t('Expired At')}
                    name="expired_at"
                    onBlur={handleBlur}
                    format="YYYY-MM-DD HH:mm:ss"
                    required
                    onChange={(date) =>
                      setFieldValue('expired_at', moment(date).format('YYYY-MM-DD HH:mm:ss'))
                    }
                    inputVariant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <Typography variant="h5" color="textPrimary">
                    {t('Earn Chops After Consume')}
                  </Typography>
                  <Switch
                    checked={values.earn_chops_after_consume}
                    color="secondary"
                    edge="start"
                    name="earn_chops_after_consume"
                    onChange={handleChange}
                    value={values.earn_chops_after_consume}
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

ConsumeChopRuleCreateForm.propTypes = {
  className: PropTypes.string,
  ranks: PropTypes.array.isRequired
};

export default ConsumeChopRuleCreateForm;
